@media (min-width: 768px) {
  .range {
    position: static;
    display: block;
    width: 426px;
    height: 40px;
    padding-top: 15px;
    box-sizing: border-box;
    margin-right: 23px;
    margin-left: 23px;
  }
  .range__scale {
    position: relative;
    height: 6px;
    width: 426px;
    background-color: #dcdcdc;
    border-radius: 6px;
  }
  .range__toggle {
    position: absolute;
    height: 34px;
    width: 34px;
    top: -13px;
    left: 197px;
    background-color: #ffffff;
    border: 1px solid #cdcdcd;
    border-radius: 50%;
    box-sizing: border-box;
  }
}
@media (min-width: 768px) and (min-width: 1440px) {
  .range__toggle {
    left: 198px;
  }
}
@media (min-width: 768px) {
  .range__toggle::before {
    position: absolute;
    content: "";
    width: 8px;
    height: 8px;
    background-color: #68b738;
    border-radius: 50%;
    top: 12px;
    left: 12px;
  }
}
